Page({

  data: {
    templateMessageResult: '',
    wxacodeSrc: '',
    wxacodeResult: '',
    showClearWXACodeCache: false,
  },

  submitTemplateMessageForm(e) {
    this.setData({
      templateMessageResult: '',
    })

    wx.cloud.callFunction({
      name: 'openapi',
      data: {
        action: 'sendTemplateMessage',
        formId: e.detail.formId,
      },
      success: res => {
        console.warn('[] [openapi] templateMessage.send ', res)
        wx.showModal({
          title: '',
          content: '',
          showCancel: false,
        })
        wx.showToast({
          title: '',
        })
        this.setData({
          templateMessageResult: JSON.stringify(res.result)
        })
      },
      fail: err => {
        wx.showToast({
          icon: 'none',
          title: '',
        })
        console.error('[] [openapi] templateMessage.send ', err)
      }
    })
  },

  onGetWXACode() {

    this.setData({
      wxacodeSrc: '',
      wxacodeResult: '',
      showClearWXACodeCache: false,
    })

    //  localStorage  ID 
    const fileID = wx.getStorageSync('wxacodeCloudID')

    if (fileID) {
      //  ID  ID
      //   ->  ->  Storage  key
      this.setData({
        wxacodeSrc: fileID,
        wxacodeResult: ` ID`,
        showClearWXACodeCache: true,
      })
      console.log(` ID${fileID}`)
    } else {
      wx.cloud.callFunction({
        name: 'openapi',
        data: {
          action: 'getWXACode',
        },
        success: res => {
          console.warn('[] [openapi] wxacode.get ', res)
          wx.showToast({
            title: '',
          })
          this.setData({
            wxacodeSrc: res.result,
            wxacodeResult: ``,
            showClearWXACodeCache: true,
          })
          wx.setStorageSync('wxacodeCloudID', res.result)
        },
        fail: err => {
          wx.showToast({
            icon: 'none',
            title: '',
          })
          console.error('[] [openapi] wxacode.get ', err)
        }
      })
    }
  },

  clearWXACodeCache() {
    wx.removeStorageSync('wxacodeCloudID')

    this.setData({
      wxacodeSrc: '',
      wxacodeResult: '',
      showClearWXACodeCache: false,
    })

    wx.showToast({
      title: '',
    })
  },

})

