const app=getApp()
Page({
  data: {

  },

  /**
   * --
   */
  onLoad: function (options) {
    if (!wx.cloud) {
      wx.showToast({
        title: '',
        icon: 'none',
        duration: 1500
      })
      return
    }
    // 
    wx.getSetting({
      success: res => {
        //console.log(res.authSetting);
        if (res.authSetting['scope.userInfo']) {
          //  getUserInfo 
          wx.cloud.callFunction({
            name: 'login',
            data: {},
            success: res => {
              //console.log('[] [login] user openid: ', res.result.openid)
              app.globalData.openid = res.result.openid
              wx.setStorageSync("myOpenId", res.result.openid);
              wx.getUserInfo({
                success: res => {
                  this.setData({
                    avatarUrl: res.userInfo.avatarUrl,
                    userInfo: res.userInfo
                  })
                  wx.setStorage({
                    key: "Userinfo",
                    data: this.data.userInfo
                  })
                  wx.setStorage({
                    key: "User_openid",
                    data: app.globalData.openid
                  })
                  
                  wx.switchTab({
                    url: '../Main_page/Main_page',
                  })
                }
              })

            },
            fail: err => {
              console.error('[] [login] ', err)
              wx.showToast({
                title: ':',
                icon: 'none',
                duration: 1500
              })
            }
          })

        }
        else{
          wx.redirectTo({
            url: '../Login/Login',
          })
        }
      }
    })

  },
})