// miniprogram/pages/openapi/cloudid/cloudid.js
Page({

  data: {
    weRunResult: '',
    userInfoResult: '',
  },

  onGetWeRunData() {
    wx.getWeRunData({
      success: res => {
        wx.cloud.callFunction({
          name: 'echo',
          data: {
            // info  event 
            info: wx.cloud.CloudID(res.cloudID),
          },
        }).then(res => {
          console.log('[onGetWeRunData]  echo ', res)

          this.setData({
            weRunResult: JSON.stringify(res.result),
          })

          wx.showToast({
            title: '',
          })
        }).catch(err => {
          console.log('[onGetWeRunData] ', err)
        })
      }
    })

  },

  onGetUserInfo(e) {
    console.log(e)
    wx.cloud.callFunction({
      name: 'openapi',
      data: {
        action: 'getOpenData',
        openData: {
          list: [
            e.detail.cloudID,
          ]
        }
      }
    }).then(res => {
      console.log('[onGetUserInfo] ', res)

      this.setData({
        userInfoResult: JSON.stringify(res.result),
      })

      wx.showToast({
        title: '',
      })
    })
  }
})
